<template>
  <div class="template-container">
    <div class="template-container-title">编辑广告组</div>

    <el-form ref="form" :model="form" :inline="true">
      <!--推广计划 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">推广计划</div>
        <div class="temp-item-main">
          <el-form-item label="所属推广计划">
            <el-select v-model="form.campaign_id" @change="adCampaignChange" filterable placeholder="选择推广计划" disabled>
              <el-option v-for="item in adCampaignsList" :key="item.campaign_id" :label="item.campaign_name" :value="item.campaign_id"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </div>
      <!--推广计划 end -->

      <!--站点 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">站点</div>
        <div class="temp-item-main">
          <label class="temp-item-label" for="">投放媒体</label>
          <div class="radio-group-inlineblock">
            <el-radio-group v-model="form.site_set" disabled>
              <el-radio class="radio-group-item" v-for="(val,key) in siteSetList" :key="key" :label="key">{{val}}</el-radio>
            </el-radio-group>
          </div>

        </div>
      </div>
      <!--站点 end -->

      <!--目标详情 begin -->
      <div class="temp-item-block" v-show='productShow'>
        <div class="temp-item-title">目标详情</div>
        <div class="temp-item-main">
          <el-form-item label="标的物">
            <el-select v-model="form.product_refs_id" filterable placeholder="选择标的物" disabled>
              <el-option v-for="item in productList" :key="item.product_refs_id" :label="item.product_name" :value="item.product_refs_id"></el-option>
            </el-select>
          </el-form-item>
          <!--子标的物详情 begin -->
          <el-form-item label="渠道包" v-show='subProductShow'>
            <el-select v-loading="loadingSubProduct" v-model="form.sub_product_refs_id" filterable placeholder="选择渠道包">
              <el-option v-for="item in subProductList" :key="item.sub_product_refs_id" :label="item.package_name" :value="item.sub_product_refs_id"></el-option>
            </el-select>
          </el-form-item>
          <!--子标的物详情 end -->
        </div>
      </div>
      <!--目标详情 end -->

      <!--定向 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">定向 </div>
        <div class="temp-item-main">
          <el-select v-model="targetId" filterable remote placeholder="定向ID" @change='changeTargetId' :remote-method="remoteMethod"
            style="width:200px" :loading="loading">
            <el-option v-for="item in targetList" :key="item.targeting_id" :label="item.targeting_name" :value="item.targeting_id"></el-option>
          </el-select>
          <el-button v-if="targetList.length" type="primary" @click="editTarget" :model="target">编辑</el-button>
          <v-target :target="target" :readonly="true"></v-target>
        </div>
      </div>
      <!--定向 end -->

      <!--排期与出价 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">排期与出价</div>
        <div class="temp-item-main">
          <div>
            <el-form-item label="活动时间">
              <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至"
                start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
              </el-date-picker>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="投放时段">
              <el-radio-group v-model="form.time_range" @change='dateRangeChange' size="medium">
                <el-radio label="0">全天</el-radio>
                <el-radio label="1">自定义</el-radio>
              </el-radio-group>
              <div v-show='timeShow'>
                <el-time-select placeholder="起始时间" v-model="form.start_time" :picker-options="{
                    start: '00:00',
                    step: '01:00',
                    end: '24:00',
                    maxTime: form.end_time
                  }">
                </el-time-select>
                <el-time-select placeholder="结束时间" v-model="form.end_time" :picker-options="{
                    start: '00:00',
                    step: '01:00',
                    end: '24:00',
                    minTime: form.start_time
                  }">
                </el-time-select>
                <a href="javascript:;" onclick="return false">高级模式</a>
              </div>
            </el-form-item>
          </div>
          <div v-show='budgetShow'>
            <label class="temp-item-label" for="">日限额：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-item-inputtext" v-model="form.daily_budget" type="text" value="" />
              <span class="temp-item-remark">元/天</span>
            </div>
          </div>
          <div>
            <el-form-item label="出价方式">
              <el-radio-group v-model="form.billing_event" @change='billingEventChange' size="medium">
                <el-radio label="BILLINGEVENT_CLICK" border v-bind:disabled="disabledCpc">CPC</el-radio>
                <el-radio label="BILLINGEVENT_IMPRESSION" border v-bind:disabled="disabledCpm">CPM</el-radio>
                <el-tooltip class="item" effect="dark" v-bind:content="ocpaMessage" placement="top-start">
                <el-radio label="BILLINGEVENT_OCPA" border v-bind:disabled="disabledOcpa">oCPA</el-radio>
                </el-tooltip>
              </el-radio-group>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="优化目标">
              <el-select v-model="form.optimization_goal" filterable placeholder="选择优化目标">
                <el-option v-for="(val,key) in optimizationGoalList" :key="key" :label="val.name" :value="key"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div>
            <label class="temp-item-label" for="">出价：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-item-inputtext" v-model="form.bid_amount" type="text" value="" />
              <span class="temp-item-remark">元/{{ amountName }}</span>
              <p class="temp-item-annotation" style="margin-left:20px;">建议出价
                <span>{{ amountRange }}</span>元/{{ amountName }}</p>
            </div>
          </div>
          <div>
            <label class="temp-item-label" for="">广告组名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" @input="adgroupNameChange" v-model="form.adgroup_name" type="text" value="" maxlength="40"
              />
              <span class="temp-item-inlayremark">{{ adgroup_name_size }}/40</span>
            </div>
          </div>
        </div>
      </div>
      <!--排期与出价 end -->
      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit">确认修改</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="50%" :modal="false">
      <v-target :target="target" :locked="true" @change="changeTarget"></v-target>
    </el-dialog>
    <div class='estimation'>
      <div class="datas">
        <div class="data-des">
          <h3>预计最大覆盖用户</h3>
          <p>
            <span class="num">{{approximate_count}}</span>&nbsp;人</p>
        </div>
        <div class="data-des">
          <h3>预计最大日曝光量</h3>
          <p>
            <span class="num">{{impression}}</span>&nbsp;次</p>
        </div>
      </div>
    </div>


  </div>

</template>

<script>
  import vTarget from '../../components/vTarget'
  export default {
    components: {
      'v-target': vTarget
    },
    created: function () {
      var params = this.$route.params || {}
      if (params.hasOwnProperty('adgroup_id')) {
        this.adgroupId = params.adgroup_id
      }
      this.getAdCommongetEnum()
      if (this.account_id) {
        this.getAdCampaigns(this.adCampaignsParams)
      }
    },
    data() {
      return {
        chosen:0,
        disabledCpc: true,
        disabledCpm: true,
        disabledOcpa: true,
        ocpaMessage: '选择标的物，达到门槛方可选择',
        submitLoading: false, // 禁止重复提交 -- loading 状态
        loading: false,
        loadingSubProduct: false,

        dialogConfirmVisible: false,
        dialogVisible: false,
        loading: false,
        targetIds: {},
        targetId: 0,
        targets: [],
        targetList: [],
        target: {
          targeting_id: 0,
          targeting_name: ''
        },
        dialogTitle: '',

        adgroupId: 0,
        adgroupData: [],
        adCampaignsList: [],
        campaignTypeList: [],
        productTypeList: [],
        optimizationGoalList: [],
        optimizationGoalConfig: [],
        productList: [],
        subProductList: [],
        productShow: false,
        subProductShow: false,
        timeShow: false,
        timeSeniorShow: false,
        budgetShow: false,
        siteSetList: {},
        siteSetConfig: {},
        bid_amount: '',
        amountName: '点击',
        amountRange: '0~0',
        approximate_count: 0, //覆盖人数
        impression: 0, // 曝光量
        adgroup_name_size: 0,
        form: {
          adgroup_id: '',
          campaign_id: '',
          campaign_type: '',
          adgroup_name: '',
          product_type: '',
          product_refs_id: '',
          sub_product_refs_id: '',
          site_set: '',
          date_range: '0',
          begin_date: '',
          end_date: '',
          time_range: '0',
          start_time: '00:00',
          end_time: '24:00',
          time_senior_type: '0',
          time_series: '',
          daily_budget: '',
          billing_event: 'BILLINGEVENT_CLICK',
          bid_amount: '',
          optimization_goal: '',
          target: {}
        },
        adCampaignsParams: {
          page: 1,
          page_size: 100,
          campaign_id: '',
          configured_status: '',
          campaign_name: '',
          start_date: '',
          end_date: ''
        },
        pickerDate: [],
        pickerOptions2: {
          shortcuts: [{
              text: '最近一周',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
                picker.$emit('pick', [start, end])
              }
            },
            {
              text: '最近一个月',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
                picker.$emit('pick', [start, end])
              }
            },
            {
              text: '最近三个月',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
                picker.$emit('pick', [start, end])
              }
            }
          ]
        }
      }
    },
    computed: {
      account_id() {
        var account = this.$store.getters.get_account_action
        return account.account_id
      },
      form_list() {
        var formList = {}
        var keyList = ['site_set', 'product_type', 'billing_event', 'optimization_goal',
          'product_refs_id', 'time_range', 'start_time', 'end_time', 'time_senior_type', 'time_series', 'target'
        ]
        for (var key in keyList) {
          formList[key] = this.form[keyList[key]]
        }
        var form = JSON.stringify(formList)
        return form
      },
      product_refs_id() {
        return this.form.product_refs_id
      }
    },
    mounted() {
      //console.log(this.listData)
      // if (this.targetList.length == 0) {
      //   this.getTargetList()
      // }
    },
    methods: {
      editTarget(item) {
        this.dialogTitle = '编辑定向包'
        this.dialogVisible = true
      },
      handleConfirmClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done()
          })
          .catch(_ => {})
      },

      changeTargetId(itemId) {
        if (itemId) {
          if (this.targetIds.hasOwnProperty(itemId)) {
            this.target = this.targetIds[itemId]
            this.form.target = this.targetIds[itemId]
          }
        }
      },

      changeTarget(item) {
        if (item) {
          if (!this.targetIds.hasOwnProperty(item.targeting_id)) {
            this.targetList.push(item)
            this.targetIds[item.targeting_id] = item
          }
          this.target = item
          this.form.target = item
          this.targetId = item.targeting_id
        }
        this.dialogVisible = false
      },

      remoteMethod(query) {
        console.log('remoteMethod');

        setTimeout(() => {
          this.getTargetList(query)
        }, 200)
      },
      updateTargetList(query) {
        this.targetList = this.targets.filter(item => {
          return (
            item.targeting_name.toLowerCase().indexOf(query.toLowerCase()) > -1
          )
        })
      },
      getTargetList(filtering) {
        console.log('getTargetList');
        var that = this
        var params = {}

        if (typeof filtering == 'number') {
          params['targeting_id'] = filtering
        } else if (typeof filtering == 'string' && filtering.length > 0) {
          params['filtering'] = filtering
        }
        params['account_id'] = this.account_id
        if (!this.account_id) {
          return
        }
        this.loading = true
        this.$axios
          .get('/Admin/Marketing_Targetings/get', {
            params: params
          })
          .then(function (response) {
            that.loading = false
            var respData = response.data
            if (respData.code == 0) {
              respData.data.list.forEach(element => {
                if (!that.targetIds.hasOwnProperty(element.targeting_id)) {
                  that.targetIds[element.targeting_id] = element
                  that.targets.push(element)
                }
              })
              if (typeof filtering == 'number') {
                if (that.targets.length == 0 || !that.targetIds.hasOwnProperty(filtering)) {
                  var el = {
                    targeting_id: filtering,
                    targeting_name: "无效ID:" + filtering
                  }
                  that.targets.push(el)
                  that.targetIds[filtering] = el
                }

                that.targetList = that.targets
                that.target = that.targetIds[filtering]
                that.form.target = that.targetIds[filtering]
                that.targetId = that.target.targeting_id

              } else if (typeof filtering == 'string') {
                that.updateTargetList(filtering)
              }
            } else {
              that.$message(respData.msg)
            }
          })
          .catch(function (err) {
            that.loading = false
            that.$message('网络繁忙，请稍后再试！')
            console.log(err)
          })
      },

      adCampaignChange(campaign_id) {
        let obj = {}
        obj = this.adCampaignsList.find(item => {
          return item.campaign_id === campaign_id
        })
        this.form.product_type = obj.product_type
        if (this.productTypeList[obj.product_type]['product_refs_id'] === 1) {
          // 需要选择标的物id
          this.productShow = true
          this.getProducts({
            product_type: obj.product_type
          })
        } else {
          // 不需要选择标的物id
          this.productShow = false
          this.form.product_refs_id = ''
        }
        this.form.campaign_type = obj.campaign_type
        if (obj.campaign_type == 'CAMPAIGN_TYPE_WECHAT_MOMENTS') {
          //日限额，仅限微信朋友圈广告
          this.budgetShow = true
        } else {
          this.budgetShow = false
        }
        // 站点
        this.siteSetList = {}
        var campaignSiteSet = this.campaignTypeList[obj.campaign_type]['site_set']
        for (var key in campaignSiteSet) {
          this.siteSetList[campaignSiteSet[key]] = this.siteSetConfig[
            campaignSiteSet[key]
          ]
        }
      },
      productChange(product_refs_id) {
        if(this.form.product_type == 'PRODUCT_TYPE_APP_ANDROID_OPEN_PLATFORM'){
          this.subProductShow = true
          this.loadingSubProduct = true
          this.subProductList = []
          this.getSubProducts({
            product_type: this.form.product_type,
            product_refs_id: product_refs_id
          })
        }else{
          this.subProductShow = false
          this.form.sub_product_refs_id = ''
        }
      },
      dateRangeChange(value) {
        if (value === '1') {
          this.timeShow = true
        } else {
          this.timeShow = false
          this.form.startTime = '00:00'
          this.form.endTime = '24:00'
        }
      },
      adgroupNameChange(e) {
        this.adgroup_name_size = this.form.adgroup_name.length
      },
      billingEventChange(value) {
        if (value == 'BILLINGEVENT_IMPRESSION') {
          this.amountName = '千次曝光'
        } else if (value == 'BILLINGEVENT_CLICK') {
          this.amountName = '点击'
        }else {
          this.amountName = '激活'
        }
        this.optimizationGoalList = {}
        for (var key in this.optimizationGoalConfig) {
          if(this.optimizationGoalConfig[key]['billing_event'] == value){
            this.optimizationGoalList[key] = this.optimizationGoalConfig[key]
          }
        }
        if (!this.optimizationGoalList.hasOwnProperty(this.form.optimization_goal)) {
          this.form.optimization_goal = ''
        }
      },
      onCancel() {
        this.$router.push('/advertising/adGroup')
      },
      onSubmit() {
        var params = {}
        params = JSON.parse(JSON.stringify(this.form)) //this.form是父组件传递的对象
        var that = this
        // 编辑时候，删除无修改项
        if (
          params['optimization_goal'] == this.adgroupData['optimization_goal']
        ) {
          params['optimization_goal'] = ''
        }
        params['account_id'] = this.account_id
        that.submitLoading = true
        this.$axios
          .post('/Admin/Marketing_AdGroups/update', params)
          .then(function (response) {
            var respData = response.data
            that.submitLoading = false
            if (respData.code == 0) {
              that.$message({
                type: 'success',
                message: respData.msg
              })
              setTimeout(function () {
                that.$router.push('/advertising/adGroup')
              }, 500)
            }else{
              that.$message.error(respData.msg)
            }
          })
          .catch(function (err) {
            that.$message('网络繁忙，请稍后再试！')
            that.submitLoading = false
            console.log(err)
          })
      },
      getAdCommongetEnum() {
        var that = this
        var params = {}
        this.$axios
          .get('/Admin/Marketing_Common/getEnum', {})
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              that.siteSetList = that.siteSetConfig = respData.data['SITE_SET']
              that.productTypeList = respData.data['PRODUCT_TYPE']
              that.campaignTypeList = respData.data['CAMPAIGN_TYPE']
              that.optimizationGoalConfig = respData.data['OPTIMIZATION_GOAL']
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      getProducts: function (params) {
        var that = this

        params['account_id'] = this.account_id
        this.$axios
          .get('/Admin/Marketing_Products/list', {
            params: params
          })
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              that.productList = respData.data
            } else {
              that.$message(respData.msg)
            }
          })
          .catch(function (err) {
            that.$message('网络繁忙，请稍后再试！')
          })
      },
      
      getSubProducts: function (params) {
        var that = this
        params['account_id'] = this.account_id
        this.$axios
          .get('/Admin/Marketing_Products/get', {
            params: params
          })
          .then(function (response) {
            var respData = response.data
            that.loadingSubProduct = false
            if (respData.code == 0) {
              that.subProductList = respData.data.subordinate_product_list
            } else {
              that.$message(respData.msg)
            }
          })
          .catch(function (err) {
            that.loadingSubProduct = false
            that.$message('网络繁忙，请稍后再试！')
          })
      },
      getAdCampaigns: function (params) {
        var that = this

        params['account_id'] = this.account_id
        this.$axios
          .get('/Admin/Marketing_Campaigns/get', {
            params: params
          })
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              if (respData.data.list || respData.data.page_info) {
                that.adCampaignsList = respData.data.list
                if (that.adgroupId != 0 && that.chosen == 0) {
                  that.chosen = 1
                  that.getAdgroup(that.adgroupId)
                }
              }
            } else {
              that.$message(respData.msg)
            }
          })
          .catch(function (err) {
            that.$message('网络繁忙，请稍后再试！')
          })
      },
      pickerDateChange() {
        var begin_date = new Date(this.pickerDate[0]),
          end_date = new Date(this.pickerDate[1])
        this.form.begin_date =
          begin_date.getFullYear() +
          '-' +
          (begin_date.getMonth() + 1) +
          '-' +
          begin_date.getDate()
        this.form.end_date =
          end_date.getFullYear() +
          '-' +
          (end_date.getMonth() + 1) +
          '-' +
          end_date.getDate()
      },
      getAdgroup(adgroup_id) {
        var that = this

        this.$axios
          .get('/Admin/Marketing_Adgroups/get', {
            params: {
              adgroup_id: adgroup_id,
              account_id: that.account_id
            }
          })
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              if (respData.data.list[0]) {
                that.adgroupData = respData.data.list[0]
                that.form.adgroup_id = respData.data.list[0]['adgroup_id']
                that.form.campaign_id = respData.data.list[0]['campaign_id']
                that.form.adgroup_name = respData.data.list[0]['adgroup_name']
                that.form.product_type = respData.data.list[0]['product_type']
                that.form.site_set = respData.data.list[0]['site_set'][0]
                that.form.begin_date = respData.data.list[0]['begin_date']
                that.form.end_date = respData.data.list[0]['end_date']
                that.pickerDate = [that.form.begin_date, that.form.end_date]
                that.form.time_series = respData.data.list[0]['time_series']
                var time_series_range = respData.data.list[0]['time_series_range']
                var weekDate = []
                var time_series = that.form.time_series
                // 判断时间格式
                if (
                  time_series ==
                  ''
                ) {
                  that.form.time_range = '0'
                } else {
                  that.form.time_range = '1'
                  that.timeShow = true
                  // 高级模式
                  if (time_series_range.time_senior_type == 1) {
                    that.timeSeniorShow = true
                  } else {
                    that.form.start_time = time_series_range.start_hour + ':00'
                    that.form.end_time = time_series_range.end_hour + ':00'
                  }
                }
                that.form.daily_budget =
                  respData.data.list[0]['daily_budget'] / 100
                that.form.bid_amount = respData.data.list[0]['bid_amount'] / 100
                that.form.optimization_goal = respData.data.list[0]['optimization_goal']
                if(respData.data.list[0]['optimization_goal'] != 'OPTIMIZATIONGOAL_CLICK' && respData.data.list[0]['optimization_goal'] != 'OPTIMIZATIONGOAL_IMPRESSION'){
                  respData.data.list[0]['billing_event'] = 'BILLINGEVENT_OCPA' // 特殊化计费类型为oCPA
                }
                if(respData.data.list[0]['billing_event'] == 'BILLINGEVENT_CLICK'){
                  that.disabledCpc = false
                }else if(respData.data.list[0]['billing_event'] == 'BILLINGEVENT_IMPRESSION'){
                  that.disabledCpm = false
                }
                that.form.billing_event = respData.data.list[0]['billing_event']
                that.billingEventChange(that.form.billing_event)
                that.form.product_refs_id = respData.data.list[0]['product_refs_id']
                that.form.sub_product_refs_id = respData.data.list[0]['sub_product_refs_id']
                that.target = {
                  targeting_id: respData.data.list[0]['targeting_id'],
                  targeting: respData.data.list[0]['targeting']
                }
                if (that.target.targeting_id > 0) {
                  that.getTargetList(that.target.targeting_id)
                }

                that.adgroupNameChange()
                that.getEstimation()
                that.adCampaignChange(that.form.campaign_id)
                that.productChange(that.form.product_refs_id)
              }
            }
          })
          .catch(function (err) {
            console.log(err)
          })
      },
      getEstimation() { // 人数及出价预估
        var that = this
        var params = {}
        params['adgroup'] = this.form
        params['account_id'] = this.account_id
        this.$axios
          .post('/Admin/Marketing_Estimation/get', params)
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              var min = respData.data.min_bid_amount / 100
              var max = respData.data.max_bid_amount / 100
              that.amountRange = min + '~' + max
              that.approximate_count = (respData.data.approximate_count + '').replace(/(?=(?!(\b))(\d{3})+$)/g, ",")
              that.impression = (respData.data.impression + '').replace(/(?=(?!(\b))(\d{3})+$)/g, ",")
            }
          })
          .catch(function (err) {
            that.$message('网络繁忙，请稍后再试！')
            console.log(err)
          })
      },
      checkOcpa() { // 查询该标的物是否满足oCPA条件
        var that = this
        var params = {}
        params['product_type'] = this.form.product_type
        params['product_refs_id'] = this.form.product_refs_id
        params['account_id'] = this.account_id
        this.$axios
          .post('/Admin/Marketing_Products/checkOcpa', params)
          .then(function (response) {
            var respData = response.data
            if (respData.code == 0) {
              that.disabledOcpa = false
            }else{
              if(that.form.billing_event == 'BILLINGEVENT_OCPA'){
                that.form.billing_event = ''
                that.form.optimization_goal = ''
              }
            }
            that.ocpaMessage = respData.msg
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    watch: {
      // 监听 account_id 发生变化
      account_id() {
        var params = this.$route.params || {}
        if (params.hasOwnProperty('adgroup_id')) {
          this.adgroupId = params.adgroup_id
        }
        // if (this.adgroupId != 0) {
        //   this.getAdgroup(this.adgroupId)
        // }
        this.getAdCampaigns(this.adCampaignsParams)
        if (this.targetList.length == 0) {
          this.getTargetList()
        }
      },

      form_list() {
        this.getEstimation()
      },
      product_refs_id() {
        this.disabledOcpa = true
        if(this.form.product_refs_id != ''){
          if(this.form.billing_event == 'BILLINGEVENT_IMPRESSION'){
            this.ocpaMessage = 'CPM广告不能转换成oCPA'
          }else{
            this.ocpaMessage = '检查APP是否达到oCPA使用门槛中...'
            this.checkOcpa()
          }
        }else{
          this.ocpaMessage = '非APP广告暂时不支持oCPA'
        }
        // this.disabledOcpa = false
      }
    }
  }

</script>


<style scoped>
  .temp-radio-item {
    width: 100%;
    margin: 0 0 20px 0;
  }

  .temp-radio-remark {
    display: inline-block;
    color: #999;
    font-size: 14px;
    margin-left: 100px;
  }

  .radio-inlineblock {
    display: inline-block;
    margin-left: 20px;
  }

  .estimation {
    width: 200px;
    padding: 15px;
    border: 5px solid transparent;
    background: #f5f6f8;
    border-radius: 9px;
    background-clip: content-box;
    position: fixed;
    top: 100px;
    right: 20px;
    _position: relative;
    z-index: 100;
    _top: 0;
  }

  .estimation .datas {
    padding: 15px;
  }

  .estimation .data-des {
    line-height: 24px;
    font-size: 15px;
    color: #555;
    margin-bottom: 26px;
  }

  .estimation .datas p {
    margin-bottom: 10px;
    line-height: 21px;
  }

  .estimation .data-des h3 {
    font-size: 13px;
    color: #888;
    margin-bottom: 12px;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.1;
  }

  .estimation .data-des .num {
    color: #404246;
    font-weight: 700;
    font-size: 19px;
  }

</style>



